'use client';

import React, { useState } from 'react';
import Layout from '@/components/Layout';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import {
  Minus,
  Plus,
  Search,
  Filter,
  Download,
  RefreshCw,
  Trash2,
  Edit,
  AlertTriangle,
  CheckCircle,
  Info
} from 'lucide-react';
import { 
  NegativeKeyword, 
  MatchType, 
  NegativeKeywordLevel 
} from '@/types';
import { useNegativeKeywords } from '@/hooks/useNegativeKeywords';

export default function NegativeKeywordsPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedLevel, setSelectedLevel] = useState<NegativeKeywordLevel | 'all'>('all');
  const [selectedMatchType, setSelectedMatchType] = useState<MatchType | 'all'>('all');
  const [selectedKeywords, setSelectedKeywords] = useState<Set<string>>(new Set());

  const {
    data: negativeKeywordsData,
    loading,
    error,
    removeNegativeKeyword,
    refresh,
  } = useNegativeKeywords();

  // Filter negative keywords based on search criteria
  const getFilteredKeywords = (keywords: NegativeKeyword[]) => {
    return keywords.filter(keyword => {
      const matchesSearch = !searchTerm || 
        keyword.keywordText.toLowerCase().includes(searchTerm.toLowerCase()) ||
        keyword.campaignName?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        keyword.adGroupName?.toLowerCase().includes(searchTerm.toLowerCase());
      
      const matchesLevel = selectedLevel === 'all' || keyword.level === selectedLevel;
      const matchesMatchType = selectedMatchType === 'all' || keyword.matchType === selectedMatchType;
      
      return matchesSearch && matchesLevel && matchesMatchType;
    });
  };

  // Get keywords by level
  const campaignKeywords = negativeKeywordsData?.data?.campaign || [];
  const adGroupKeywords = negativeKeywordsData?.data?.adGroup || [];
  const sharedKeywords = negativeKeywordsData?.data?.shared || [];

  const filteredCampaignKeywords = getFilteredKeywords(campaignKeywords);
  const filteredAdGroupKeywords = getFilteredKeywords(adGroupKeywords);
  const filteredSharedKeywords = getFilteredKeywords(sharedKeywords);

  // Handle keyword selection
  const handleKeywordSelect = (keywordId: string) => {
    setSelectedKeywords(prev => {
      const newSet = new Set(prev);
      if (newSet.has(keywordId)) {
        newSet.delete(keywordId);
      } else {
        newSet.add(keywordId);
      }
      return newSet;
    });
  };

  // Handle select all
  const handleSelectAll = (keywords: NegativeKeyword[]) => {
    const allIds = keywords.map(k => k.id);
    const allSelected = allIds.every(id => selectedKeywords.has(id));
    
    if (allSelected) {
      setSelectedKeywords(prev => {
        const newSet = new Set(prev);
        allIds.forEach(id => newSet.delete(id));
        return newSet;
      });
    } else {
      setSelectedKeywords(prev => {
        const newSet = new Set(prev);
        allIds.forEach(id => newSet.add(id));
        return newSet;
      });
    }
  };

  // Handle keyword removal
  const handleRemoveKeyword = async (keywordId: string) => {
    if (confirm('Are you sure you want to remove this negative keyword?')) {
      const success = await removeNegativeKeyword(keywordId);
      if (success) {
        setSelectedKeywords(prev => {
          const newSet = new Set(prev);
          newSet.delete(keywordId);
          return newSet;
        });
      }
    }
  };

  // Handle bulk removal
  const handleBulkRemove = async () => {
    if (selectedKeywords.size === 0) return;
    
    if (confirm(`Are you sure you want to remove ${selectedKeywords.size} negative keywords?`)) {
      const promises = Array.from(selectedKeywords).map(id => removeNegativeKeyword(id));
      await Promise.all(promises);
      setSelectedKeywords(new Set());
    }
  };

  // Export keywords
  const handleExport = (keywords: NegativeKeyword[], filename: string) => {
    const csvContent = [
      ['Keyword', 'Match Type', 'Level', 'Campaign', 'Ad Group', 'Shared List', 'Added Date'],
      ...keywords.map(k => [
        k.keywordText,
        k.matchType,
        k.level,
        k.campaignName || '',
        k.adGroupName || '',
        k.sharedListName || '',
        k.addedDate,
      ]),
    ].map(row => row.join(',')).join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    a.click();
    window.URL.revokeObjectURL(url);
  };

  // Format date
  const formatDate = (dateString: string) => {
    if (!dateString) return 'N/A';
    try {
      return new Date(dateString).toLocaleDateString();
    } catch (error) {
      return 'Invalid Date';
    }
  };

  // Get match type badge variant
  const getMatchTypeBadgeVariant = (matchType: MatchType) => {
    switch (matchType) {
      case 'EXACT':
        return 'default';
      case 'PHRASE':
        return 'secondary';
      case 'BROAD':
        return 'outline';
      default:
        return 'default';
    }
  };

  // Get level badge variant
  const getLevelBadgeVariant = (level: NegativeKeywordLevel) => {
    switch (level) {
      case 'CAMPAIGN':
        return 'default';
      case 'AD_GROUP':
        return 'secondary';
      case 'SHARED_LIST':
        return 'outline';
      default:
        return 'default';
    }
  };

  if (loading) {
    return (
      <Layout>
        <div className="space-y-6">
          <div className="h-8 bg-gray-200 rounded animate-pulse"></div>
          <div className="h-64 bg-gray-200 rounded animate-pulse"></div>
        </div>
      </Layout>
    );
  }

  if (error) {
    return (
      <Layout>
        <Card className="border-red-200 bg-red-50">
          <CardContent className="pt-6">
            <div className="flex items-center space-x-2 text-red-800">
              <AlertTriangle className="h-5 w-5" />
              <span>{error}</span>
            </div>
          </CardContent>
        </Card>
      </Layout>
    );
  }

  return (
    <Layout>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Negative Keywords</h1>
            <p className="text-gray-600 mt-1">
              Manage your negative keyword lists across campaigns, ad groups, and shared lists
            </p>
          </div>
          <div className="flex items-center space-x-3">
            <Button onClick={refresh} disabled={loading}>
              <RefreshCw className={`h-4 w-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
              Refresh
            </Button>
          </div>
        </div>

        {/* Filters */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <Filter className="h-5 w-5" />
              <span>Filters</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              <div>
                <label className="text-sm font-medium text-gray-700">Search</label>
                <div className="relative mt-1">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    placeholder="Search keywords..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="pl-10"
                  />
                </div>
              </div>
              <div>
                <label className="text-sm font-medium text-gray-700">Level</label>
                <Select value={selectedLevel} onValueChange={(value: any) => setSelectedLevel(value)}>
                  <SelectTrigger className="mt-1">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Levels</SelectItem>
                    <SelectItem value="CAMPAIGN">Campaign</SelectItem>
                    <SelectItem value="AD_GROUP">Ad Group</SelectItem>
                    <SelectItem value="SHARED_LIST">Shared List</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div>
                <label className="text-sm font-medium text-gray-700">Match Type</label>
                <Select value={selectedMatchType} onValueChange={(value: any) => setSelectedMatchType(value)}>
                  <SelectTrigger className="mt-1">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Types</SelectItem>
                    <SelectItem value="EXACT">Exact</SelectItem>
                    <SelectItem value="PHRASE">Phrase</SelectItem>
                    <SelectItem value="BROAD">Broad</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="flex items-end">
                <Button
                  variant="outline"
                  onClick={() => {
                    setSearchTerm('');
                    setSelectedLevel('all');
                    setSelectedMatchType('all');
                  }}
                >
                  Clear Filters
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Bulk Actions */}
        {selectedKeywords.size > 0 && (
          <Card className="border-blue-200 bg-blue-50">
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-2">
                  <Info className="h-5 w-5 text-blue-600" />
                  <span className="text-blue-800">
                    {selectedKeywords.size} keyword{selectedKeywords.size !== 1 ? 's' : ''} selected
                  </span>
                </div>
                <div className="flex space-x-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => setSelectedKeywords(new Set())}
                  >
                    Clear Selection
                  </Button>
                  <Button
                    variant="destructive"
                    size="sm"
                    onClick={handleBulkRemove}
                  >
                    <Trash2 className="h-4 w-4 mr-1" />
                    Remove Selected
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Tabs for different keyword levels */}
        <Tabs defaultValue="campaign" className="space-y-6">
          <TabsList>
            <TabsTrigger value="campaign">
              Campaign Level
              <Badge variant="secondary" className="ml-2">
                {filteredCampaignKeywords.length}
              </Badge>
            </TabsTrigger>
            <TabsTrigger value="adgroup">
              Ad Group Level
              <Badge variant="secondary" className="ml-2">
                {filteredAdGroupKeywords.length}
              </Badge>
            </TabsTrigger>
            <TabsTrigger value="shared">
              Shared Lists
              <Badge variant="secondary" className="ml-2">
                {filteredSharedKeywords.length}
              </Badge>
            </TabsTrigger>
          </TabsList>

          {/* Campaign Level Keywords */}
          <TabsContent value="campaign">
            <Card>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle>Campaign Level Negative Keywords</CardTitle>
                  <div className="flex space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleSelectAll(filteredCampaignKeywords)}
                    >
                      {filteredCampaignKeywords.every(k => selectedKeywords.has(k.id)) ? 'Deselect All' : 'Select All'}
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleExport(filteredCampaignKeywords, 'campaign-negative-keywords.csv')}
                    >
                      <Download className="h-4 w-4 mr-1" />
                      Export
                    </Button>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="rounded-md border">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead className="w-12">
                          <input
                            type="checkbox"
                            checked={filteredCampaignKeywords.length > 0 && filteredCampaignKeywords.every(k => selectedKeywords.has(k.id))}
                            onChange={() => handleSelectAll(filteredCampaignKeywords)}
                            className="rounded border-gray-300"
                          />
                        </TableHead>
                        <TableHead>Keyword</TableHead>
                        <TableHead>Match Type</TableHead>
                        <TableHead>Campaign</TableHead>
                        <TableHead>Added Date</TableHead>
                        <TableHead className="text-right">Actions</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {filteredCampaignKeywords.map((keyword) => (
                        <TableRow key={keyword.id}>
                          <TableCell>
                            <input
                              type="checkbox"
                              checked={selectedKeywords.has(keyword.id)}
                              onChange={() => handleKeywordSelect(keyword.id)}
                              className="rounded border-gray-300"
                            />
                          </TableCell>
                          <TableCell className="font-medium">{keyword.keywordText}</TableCell>
                          <TableCell>
                            <Badge variant={getMatchTypeBadgeVariant(keyword.matchType)}>
                              {keyword.matchType}
                            </Badge>
                          </TableCell>
                          <TableCell>{keyword.campaignName}</TableCell>
                          <TableCell>{formatDate(keyword.addedDate || '')}</TableCell>
                          <TableCell className="text-right">
                            <Button
                              variant="ghost"
                              size="sm"
                              onClick={() => handleRemoveKeyword(keyword.id)}
                            >
                              <Trash2 className="h-4 w-4" />
                            </Button>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Ad Group Level Keywords */}
          <TabsContent value="adgroup">
            <Card>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle>Ad Group Level Negative Keywords</CardTitle>
                  <div className="flex space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleSelectAll(filteredAdGroupKeywords)}
                    >
                      {filteredAdGroupKeywords.every(k => selectedKeywords.has(k.id)) ? 'Deselect All' : 'Select All'}
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleExport(filteredAdGroupKeywords, 'adgroup-negative-keywords.csv')}
                    >
                      <Download className="h-4 w-4 mr-1" />
                      Export
                    </Button>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="rounded-md border">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead className="w-12">
                          <input
                            type="checkbox"
                            checked={filteredAdGroupKeywords.length > 0 && filteredAdGroupKeywords.every(k => selectedKeywords.has(k.id))}
                            onChange={() => handleSelectAll(filteredAdGroupKeywords)}
                            className="rounded border-gray-300"
                          />
                        </TableHead>
                        <TableHead>Keyword</TableHead>
                        <TableHead>Match Type</TableHead>
                        <TableHead>Campaign</TableHead>
                        <TableHead>Ad Group</TableHead>
                        <TableHead>Added Date</TableHead>
                        <TableHead className="text-right">Actions</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {filteredAdGroupKeywords.map((keyword) => (
                        <TableRow key={keyword.id}>
                          <TableCell>
                            <input
                              type="checkbox"
                              checked={selectedKeywords.has(keyword.id)}
                              onChange={() => handleKeywordSelect(keyword.id)}
                              className="rounded border-gray-300"
                            />
                          </TableCell>
                          <TableCell className="font-medium">{keyword.keywordText}</TableCell>
                          <TableCell>
                            <Badge variant={getMatchTypeBadgeVariant(keyword.matchType)}>
                              {keyword.matchType}
                            </Badge>
                          </TableCell>
                          <TableCell>{keyword.campaignName}</TableCell>
                          <TableCell>{keyword.adGroupName}</TableCell>
                          <TableCell>{formatDate(keyword.addedDate || '')}</TableCell>
                          <TableCell className="text-right">
                            <Button
                              variant="ghost"
                              size="sm"
                              onClick={() => handleRemoveKeyword(keyword.id)}
                            >
                              <Trash2 className="h-4 w-4" />
                            </Button>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Shared List Keywords */}
          <TabsContent value="shared">
            <Card>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle>Shared List Negative Keywords</CardTitle>
                  <div className="flex space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleSelectAll(filteredSharedKeywords)}
                    >
                      {filteredSharedKeywords.every(k => selectedKeywords.has(k.id)) ? 'Deselect All' : 'Select All'}
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleExport(filteredSharedKeywords, 'shared-negative-keywords.csv')}
                    >
                      <Download className="h-4 w-4 mr-1" />
                      Export
                    </Button>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="rounded-md border">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead className="w-12">
                          <input
                            type="checkbox"
                            checked={filteredSharedKeywords.length > 0 && filteredSharedKeywords.every(k => selectedKeywords.has(k.id))}
                            onChange={() => handleSelectAll(filteredSharedKeywords)}
                            className="rounded border-gray-300"
                          />
                        </TableHead>
                        <TableHead>Keyword</TableHead>
                        <TableHead>Match Type</TableHead>
                        <TableHead>Shared List</TableHead>
                        <TableHead>Added Date</TableHead>
                        <TableHead className="text-right">Actions</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {filteredSharedKeywords.map((keyword) => (
                        <TableRow key={keyword.id}>
                          <TableCell>
                            <input
                              type="checkbox"
                              checked={selectedKeywords.has(keyword.id)}
                              onChange={() => handleKeywordSelect(keyword.id)}
                              className="rounded border-gray-300"
                            />
                          </TableCell>
                          <TableCell className="font-medium">{keyword.keywordText}</TableCell>
                          <TableCell>
                            <Badge variant={getMatchTypeBadgeVariant(keyword.matchType)}>
                              {keyword.matchType}
                            </Badge>
                          </TableCell>
                          <TableCell>{keyword.sharedListName}</TableCell>
                          <TableCell>{formatDate(keyword.addedDate || '')}</TableCell>
                          <TableCell className="text-right">
                            <Button
                              variant="ghost"
                              size="sm"
                              onClick={() => handleRemoveKeyword(keyword.id)}
                            >
                              <Trash2 className="h-4 w-4" />
                            </Button>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </Layout>
  );
}
